import React, { Fragment } from "react";
import PropTypes from "prop-types";

import { Checkbox, Tooltip } from "@salesforce/design-system-react";

const FieldLevelHelpLabel = ({ label, helpText }) => (
  <Fragment>
    <span class="slds-form-element__label">{label}</span>
    {helpText !== null && (
      <Tooltip
        content={helpText}
        align="top left"
        variant="learnMore"
        dialogClassName="dialog-classname"
      />
    )}
  </Fragment>
);

const StatefulCheckbox = ({ id, label, helpText = null, value = false, onToggle }) => (
  <Fragment>
    <FieldLevelHelpLabel label={label} helpText={helpText} />
    <div className="slds-form-element__control">
      <Checkbox variant="toggle" checked={value} onChange={() => onToggle(id, !value)} />
    </div>
  </Fragment>
);

export default StatefulCheckbox;
