import React, { useState } from "react";
import PropTypes from "prop-types";
import { Switch, Route, useHistory } from "react-router-dom";

import { VerticalNavigation, BrandBand } from "@salesforce/design-system-react";

import CLOUD_MENU from "../../consts/dedup/cloudMenu";
import { ToastContextProvider } from "../../contexts/toastContext";
import styles from "./Cloud.module.css";

const propTypes = {
  routes: PropTypes.arrayOf(PropTypes.object).isRequired,
  exact: PropTypes.bool,
  path: PropTypes.string,
  isEmbedded: PropTypes.bool
};

const defaultProps = {
  exact: false,
  path: null,
  isEmbedded: false
};

const Cloud = ({ routes, exact, path, isEmbedded, ...props }) => {
  const history = useHistory();
  const [selectedId, setSelectedId] = useState("configurations");

  const onSelect = ({ route, id }) => {
    history.push(route);
    setSelectedId(id);
  };

  return (
    <div className="slds-grid" id="grax-cloud">
      <div className={`slds-col slds-size_1-of-4 ${styles.verticalNavigation}`}>
        <VerticalNavigation
          id="grax-cloud-navigation"
          categories={CLOUD_MENU}
          selectedId={selectedId}
          onSelect={(_event, data) => onSelect(data.item)}
        />
      </div>
      <div className="slds-col slds-size_3-of-4">
        <ToastContextProvider>
          <BrandBand
            className="custom-brand-band-class slds-p-around_medium"
            id="brand-band-lightning-blue"
            theme="lightning-blue"
          >
            <Switch>
              {routes.map((route, index) => (
                <Route
                  key={`cloud-sub-route-${index}`}
                  path={route.path}
                  exact={exact}
                  render={props => (
                    <route.component routes={route.routes} exact={route.exact} {...props} />
                  )}
                />
              ))}
            </Switch>
          </BrandBand>
        </ToastContextProvider>
      </div>
    </div>
  );
};

Cloud.propTypes = propTypes;
Cloud.defaultProps = defaultProps;

export default Cloud;
