import React from "react";
import { Button } from "@salesforce/design-system-react";

import useSaveBasicConfiguration from "../../../../hooks/configuration/useSaveBasicConfiguration";

export const BasicConfigSaveButton = ({ formRef }) => {
  const saveConfig = useSaveBasicConfiguration();

  return (
    <Button
      type="submit"
      label={"Save"}
      onClick={() => {
        if (formRef.current.checkValidity() === true) saveConfig();
      }}
    />
  );
};

export default BasicConfigSaveButton;
