import React from "react";

import PropTypes from "prop-types";

import withProtectedExpandableSection from "./withProtectedExpandableSection";
import ProtectedInput from "./ProtectedInput";

import { useFormDispatch } from "../../../../../hooks/useHandleForm";
import { ADVANCED_CONFIG_SET_VALUE } from "../../../../../consts/configurations/configurationsActions";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";

const propTypes = {
  storageOff: PropTypes.bool.isRequired,
  dispatch: PropTypes.func.isRequired
};

const Other = ({ storageOff, dispatch }) => {
  const onToggle = useFormDispatch(ADVANCED_CONFIG_SET_VALUE, () => {});

  return (
    <>
      <div className="slds-grid slds-gutters_small">
        <ProtectedInput
          label="Async Interval"
          helpText="This will determine the interval that the asynchronous heroku dyno processes run at"
          variableName="apiEndpoint"
          value=""
          required
          type="number"
          dispatch={dispatch}
        />
        <div className="slds-col slds-size_1-of-3">
          <StatefulCheckbox
            label="Turn Off Storage"
            helpText="Do not turn off storage unless absolutely necessary, as this could cause extreme data loss"
            value={storageOff}
            id="storageOff"
            onToggle={onToggle}
          />
        </div>
      </div>
    </>
  );
};

Other.propTypes = propTypes;

export default withProtectedExpandableSection("Other")(Other);
