import GraxClient from "./GraxClient";
import GraxUnprocessibleEntityError from "./errors/GraxUnprocessibleEntityError";
import { VENDOR_VALIDATION_ENDPOINT } from "../consts/addresses";

class VendorConnection {
  constructor({ credentials, type, label }) {
    this.graxClient = new GraxClient();
    this.credentials = credentials;
    this.type = type;
    //this.label = label;
  }

  async validate() {
    try {
      const response = await this.graxClient.fetch(VENDOR_VALIDATION_ENDPOINT, {
        method: "POST",
        body: JSON.stringify({ credentials: this.credentials, type: this.type })
      });
      if (!!response.userInfo) {
        this.credentials = {
          ...this.credentials,
          organizationId: response.userInfo.organizationId
        };
      }
      return this.credentials;
    } catch (err) {
      if (err instanceof GraxUnprocessibleEntityError) {
        return err.details;
      } else {
        throw err;
      }
    }
  }
}

export default VendorConnection;
