import React from "react";
import PropTypes from "prop-types";
import { Checkbox } from "@salesforce/design-system-react";
import AwsRegionCombobox from "../../../../commons/AwsRegionCombobox";
import StatefulInput from "../../../../commons/StatefulInput";
import EnableOAuth from "./EnableOAuth";

const propTypes = {
  s3CompatibleEndpoint: PropTypes.string.isRequired,
  bucket: PropTypes.string.isRequired,
  accessKey: PropTypes.string.isRequired,
  secretAccessKey: PropTypes.string.isRequired
};

const S3Compatible = ({
  s3CompatibleEndpoint,
  bucket,
  accessKey,
  secretAccessKey,
  enableOAuth,
  clientId,
  tokenUrl,
  clientSecret,
  region,
  onBlur,
  onToggle,
  handleCombobox
}) => (
  <>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Endpoint"
          required
          value={s3CompatibleEndpoint}
          id="s3CompatibleEndpoint"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="S3 Bucket Name" required value={bucket} id="bucket" onBlur={onBlur} />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <AwsRegionCombobox
          label="S3 Region"
          value={region}
          onChange={selections => handleCombobox("region", selections)}
        />
      </div>
    </div>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Access Key ID"
          required
          secured
          value={accessKey}
          id="accessKey"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Secret Access Key"
          required
          secured
          value={secretAccessKey}
          id="secretAccessKey"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <span className="slds-form-element__label">Enable OAuth</span>
        <div className="slds-form-element__control">
          <Checkbox
            variant="toggle"
            checked={enableOAuth}
            onChange={() => onToggle("enableOAuth", !enableOAuth)}
          />
        </div>
      </div>
    </div>
    {enableOAuth === true && (
      <EnableOAuth
        clientId={clientId}
        tokenUrl={tokenUrl}
        clientSecret={clientSecret}
        onBlur={onBlur}
      />
    )}
  </>
);

S3Compatible.propTypes = propTypes;

export default S3Compatible;
