import React from "react";
import { Button } from "@salesforce/design-system-react";

import useSaveAdvancedConfiguration from "../../../../hooks/configuration/useSaveAdvancedConfiguration";

export const SaveAdvConfigurationButton = ({ formRef }) => {
  const saveAdvancedConfig = useSaveAdvancedConfiguration();

  return (
    <Button
      type="submit"
      label={"Save"}
      onClick={() => {
        if (formRef.current.checkValidity() === true) saveAdvancedConfig();
      }}
    />
  );
};

export default SaveAdvConfigurationButton;
