import React from "react";

import {
  CLOUD_STORAGE_PROVIDERS_AWS_S3,
  CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE,
  CLOUD_STORAGE_PROVIDERS_AZURE,
  CLOUD_STORAGE_PROVIDERS_GOOGLE_CLOUD_PLATFORM,
  CLOUD_STORAGE_PROVIDERS_MINIO
} from "../../../../../consts/configurations/cloudStorageProviders";
import { QUICK_START_FORM_SET_VALUE } from "../../../../../consts/configurations/configurationsActions";

import {
  useHandleInput,
  useHandleCombobox,
  useFormDispatch
} from "../../../../../hooks/useHandleForm";

import S3Compatible from "./S3Compatible";
import AwsS3Storage from "./AwsS3Storage";
import AzureBLOB from "./AzureBLOB";
import GoogleCloudPlatform from "./GoogleCloudPlatform";
import MinIOStorage from "./MinIOStorage";

const ProviderFactory = props => {
  const onBlur = useHandleInput(QUICK_START_FORM_SET_VALUE, props.dispatch);
  const onToggle = useFormDispatch(QUICK_START_FORM_SET_VALUE, props.dispatch);
  const handleCombobox = useHandleCombobox(QUICK_START_FORM_SET_VALUE, props.dispatch);

  switch (props.selectedProvider) {
    case CLOUD_STORAGE_PROVIDERS_AWS_S3:
      return (
        <AwsS3Storage
          storageOff={props.storageOff}
          provider={props.provider}
          name={props.name}
          accessKey={props.accessKey}
          secretAccessKey={props.secretAccessKey}
          bucket={props.bucket}
          region={props.region}
          s3AssumeRoleEnabled={props.s3AssumeRoleEnabled}
          serverSideEncryptionKmsKey={props.serverSideEncryptionKmsKey}
          serverSideEncryption={props.serverSideEncryption}
          onBlur={onBlur}
          onToggle={onToggle}
          handleCombobox={handleCombobox}
        />
      );
    case CLOUD_STORAGE_PROVIDERS_S3_COMPATIBLE:
      return (
        <S3Compatible
          s3CompatibleEndpoint={props.s3CompatibleEndpoint}
          bucket={props.bucket}
          region={props.region}
          accessKey={props.accessKey}
          secretAccessKey={props.secretAccessKey}
          enableOAuth={props.enableOAuth}
          clientId={props.clientId}
          tokenUrl={props.tokenUrl}
          clientSecret={props.clientSecret}
          onBlur={onBlur}
          onToggle={onToggle}
          handleCombobox={handleCombobox}
        />
      );
    case CLOUD_STORAGE_PROVIDERS_AZURE:
      return (
        <AzureBLOB
          storageAccountName={props.storageAccountName}
          containerName={props.containerName}
          accountAccessKey={props.accountAccessKey}
          onBlur={onBlur}
        />
      );
    case CLOUD_STORAGE_PROVIDERS_GOOGLE_CLOUD_PLATFORM:
      return (
        <GoogleCloudPlatform
          googleProjectId={props.googleProjectId}
          googleBucketName={props.googleBucketName}
          googlePrivateKeyId={props.googlePrivateKeyId}
          googlePrivateKey={props.googlePrivateKey}
          googleClientEmail={props.googleClientEmail}
          onBlur={onBlur}
        />
      );
    case CLOUD_STORAGE_PROVIDERS_MINIO:
      return <MinIOStorage />;
    default:
      return null;
  }
};

export default ProviderFactory;
