import React from "react";

import { ExpandableSection, Button } from "@salesforce/design-system-react";
import ProviderFactory from "./ProviderFactory";
import GRAXButtonGroup from "../../../../commons/ButtonGroup";
import useCloudStorageForm from "../../../../../hooks/configuration/useCloudStorageForm";
import { QUICK_START_FORM_SET_PROVIDER } from "../../../../../consts/configurations/configurationsActions";
import VendorValidityMessage from "../../../../commons/VendorValidityMessage";
import { useVendorValidityStore } from "../../../../../contexts/VendorValidityContext";

const CloudStorageForm = () => {
  const [{ selectedProvider, provider, options }, { dispatch }] = useCloudStorageForm();
  const { vendorValidationStatus, validationDispatch } = useVendorValidityStore();
  const validateVendorCreds = () => {
    validationDispatch([{ type: "storage", credentials: { ...provider } }]);
  };

  return (
    <ExpandableSection
      title="Cloud Storage Provider"
      className="slds-m-bottom_medium slds-m-top_none"
      nonCollapsible
    >
      <VendorValidityMessage validity={{ storage: vendorValidationStatus.storage }} />
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_2-of-3">
          <GRAXButtonGroup
            options={options}
            value={selectedProvider}
            onClick={provider =>
              dispatch({
                type: QUICK_START_FORM_SET_PROVIDER,
                payload: { provider }
              })
            }
          />
        </div>
      </div>
      <ProviderFactory selectedProvider={selectedProvider} {...provider} dispatch={dispatch} />
      <div className="slds-grid slds-gutters_small slds-m-top_small">
        <div className="slds-col slds-size_1-of-3">
          <Button label="Validate Storage Connection" onClick={validateVendorCreds} />
        </div>
      </div>
    </ExpandableSection>
  );
};

export default CloudStorageForm;
